import React, { Component } from 'react';
// import Card from 'react-bootstrap/Button'
import './App.css';

export default class App extends Component {

  constructor() {
    super();
    this.state = {
      Search: "Recent",
      pageNo: 20,
      Data: [],
    };
  }

  componentDidMount() {
    this.LoadData();
  }

  LoadData = () => {
    const key = '4e44d9029b1270a757cddc766a1bcb63';
    const Def_URL = 'https://api.themoviedb.org/3/movie/popular?api_key=' + key + '&language=en-US&page=1';

    fetch(Def_URL)
      .then(function (response) {
        return response.json();
      })

      .then(function (json) {
        let array = json.results.map((jsonpic) => {
          var Picture_Path = 'https://image.tmdb.org/t/p/w500' + jsonpic.poster_path;
          var title = jsonpic.title;
          var overview = jsonpic.overview;
          var release_date = jsonpic.release_date;
          var vote = jsonpic.vote_average;
          var popularity = jsonpic.popularity / 100;


          return (
            <>
              <h2>{title}</h2>
              <h3>Release Date: {release_date}</h3>
              <p>Popularity: {popularity}</p>
              <p>Rating: {vote}</p>
              <img className="pictures" src={Picture_Path}></img>
              <p>{overview}</p>
            </>
          )
        })

        this.setState({ Data: array });
      }.bind(this))
  }



  render() {

    return (

      <div className="App">

        <header className="App-header">

          <h1>Popular Movies</h1>

        </header>

        <div className="Data">
          {this.state.Data}
        </div>

      </div>

    );
  }
}